import './App.css';
import React from 'react';
import { Box, Button, Grid, Stack, Tab, Tabs } from '@mui/material';
import * as Tone from 'tone';

const synth = new Tone.PolySynth(Tone.Synth, { 
  oscillator: { type: "triangle" },
  envelope: {
    attack: 0.05,
    decay: 0.1,
    sustain: 1.0,
    release: 1.0,
  }
}).toDestination();
const keyNames = ["C", "Db", "D", "Eb", "E", "F", "Gb", "G", "Ab", "A", "Bb", "B"];
const defaultKeys = ["C4", "D4", "E4", "F4", "G4", "A4", "B4"];
const keyButtonStyle = { 
  textTransform: 'none', 
  borderRadius: 0, 
  height: "100%", 
  width: "100%", 
  minWidth: 0, 
  py: 1 }
const playKey = async (k) => { synth.triggerAttackRelease(k, 0.5); }

class RandomToneGenerator {
  constructor() {
    let letter = ["C", "D", "E", "F", "G", "A", "B"];
    let num = [4];
    this.keys = num.map((n) => {
      return letter.map((a) => { return a + n })
    }).flat();
  }

  setKeys = (keyActivity) => {
    let keys = [];
    for(let key in keyActivity) {
      if(keyActivity[key]) {
        keys.push(key);
      }
    }
    this.keys = keys;
  }

  getKey = () => {
    return this.keys[Math.random() * this.keys.length | 0]
  }
}

class KeySelector extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    let keys = [];
    for (let k in this.props.allKeys) {
      let key = this.props.allKeys[k]
      let variant = this.props.keyActivity[key] ? "contained" : "outlined";
      keys.push(
        <Grid item xs={1} key={key}>
          <Button
            disableElevation
            variant={variant}
            onClick={() => this.props.toggleKeyActivity(key)}
            sx={keyButtonStyle} >
            {key}
          </Button>
        </Grid>);
    }
    return <Grid container columns={12}>{keys}</Grid>;
  }
}

class KeyBoard extends React.Component {
  constructor(props) {
    super(props);
  }

  onKeyClick = (key) => { return () => { playKey(key); } }

  render() {
    let keys = [];
    for (let k in this.props.allKeys) {
      let key = this.props.allKeys[k]
        keys.push(
          <Grid item xs={1} key={key}>
            <Button
              variant="outlined"
              onClick={this.onKeyClick(key)}
              sx={keyButtonStyle}>
              {key}
            </Button>
          </Grid>);
    }

    return <Grid container columns={12}>{keys}</Grid>;
  }
}

class AnswerBoard extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    let keys = [];
    for (let k in this.props.allKeys) {
      let key = this.props.allKeys[k]
      let disabled = !this.props.keyActivity[key];
      let variant = (this.props.highlightAnswer && key == this.props.answer) ? 'contained' : 'outlined';
      keys.push(
        <Grid item xs={1} key={key}>
          <Button
            disableElevation
            variant={variant}
            disabled={disabled}
            onClick={() => this.props.commitAnswer(key)}
            sx={keyButtonStyle}>
            {key}
          </Button>
        </Grid>);
      }

    return <Grid container columns={12} columnSpacing={0}>{keys}</Grid>;
  }
}

class PerfectCatch extends React.Component {
  constructor(props) {
    super(props);

    let keys = []
    for(let i = 2; i <= 6; ++i) {
      for (let a in keyNames) {
        let key = keyNames[a] + i;
        keys.push(key);
      }
    }

    this.allKeys = keys;

    let keyActivity = JSON.parse(localStorage.getItem("keyActivity"));
    if (!keyActivity) {
      keyActivity = {};
      for (let i in keys) {
        keyActivity[keys[i]] = false;
      }
      for (let k in defaultKeys) {
        keyActivity[defaultKeys[k]] = true;
      }
    }

    this.keyGenerator = new RandomToneGenerator();
    this.keyGenerator.setKeys(keyActivity);
    let firstKey = this.keyGenerator.getKey();
  
    this.state = {
      keyActivity: keyActivity,
      currentKey: firstKey,
      lastGuessCorrect: true,
      activeTab: 1,
      totalKeys: 0,
      catchedKeys: 0,
    }
  }

  toggleKeyActivity = (key) => { 
      let keyActivity = this.state.keyActivity;
      keyActivity[key] = ! keyActivity[key];
      localStorage.setItem("keyActivity", JSON.stringify(keyActivity));
      this.keyGenerator.setKeys(keyActivity);
      let newKey = this.keyGenerator.getKey();
      playKey(key);
      this.setState({ 
        keyActivity: keyActivity, 
        currentKey: newKey, 
        lastGuessCorrect: true, 
        totalKeys: 0, 
        catchedKeys: 0 });
  }

  commitAnswer = (key) => {
      let catchedKeys = this.state.catchedKeys;
      let totalKeys = this.state.totalKeys;
      let lastGuessCorrect = this.state.lastGuessCorrect;
      let currentKey = this.state.currentKey;

      if (key == currentKey) {
        if (lastGuessCorrect) {
          catchedKeys = catchedKeys + 1;
          totalKeys = totalKeys + 1;
        }
        lastGuessCorrect = true;
        currentKey = this.keyGenerator.getKey();
      } else {
        if (lastGuessCorrect) {
          totalKeys = totalKeys + 1;
        }
        lastGuessCorrect = false;
      }
      playKey(currentKey);
      this.setState({ currentKey, lastGuessCorrect, totalKeys, catchedKeys });
  }

  onTabChange = (event, value) => {
    this.setState({ activeTab: value });
    if(value == 1) {
      playKey(this.state.currentKey);
    }
  }


  render() {
    let playButton =
      <Button 
        variant="outlined"
        onClick={()=>{playKey(this.state.currentKey)}}>
        Play Again
        </Button>;

    let tab;
    switch (this.state.activeTab) {
      case 0:
        tab =
          <Stack alignItems="center">
            <KeySelector 
              allKeys={this.allKeys} 
              keyActivity={this.state.keyActivity} 
              toggleKeyActivity={this.toggleKeyActivity} />
            <Box>Stat: {this.state.catchedKeys} / {this.state.totalKeys}</Box>
          </Stack>
        break;
      case 1:
        tab =
          <Stack alignItems="center">
            <AnswerBoard
              allKeys={this.allKeys}
              keyActivity={this.state.keyActivity}
              commitAnswer={this.commitAnswer}
              answer={this.state.currentKey}
              highlightAnswer={!this.state.lastGuessCorrect} />
            {playButton}
          </Stack>
        break;
      case 2:
        tab =
          <Stack alignItems="center">
            <KeyBoard allKeys={this.allKeys} />
          </Stack>
    }
    return (
      <Stack>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs 
            value={this.state.activeTab} 
            onChange={this.onTabChange} 
            centered variant="fullWidth">
            <Tab label="Settings" value={0} />
            <Tab label="Catch" value={1} />
            <Tab label="Keyboard" value={2} />
          </Tabs>
        </Box>
        {tab}
      </Stack>
    )
  }
}

function App() {
  return (
    <Box
      display="flex"
      justifyContent="center"
      minHeight="100vh">
      <Stack alignItems="center" maxWidth={800}>
        <PerfectCatch />
      </Stack>
    </Box>
  );
}

export default App;
